.partners {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 30px;

	&__partner {
		//background-color: $darker-blue;
		border-radius: 5px;
		//box-shadow: $box-shadow;
		padding: 20px;
		transition: transform 0.2s;

		&:hover {
			transform: scale(1.02);
		}

		.partner__link {
			.partner__image {
				background-size: contain;
				background-repeat: no-repeat;
				background-position: 50%;
				width: 100%;
				min-height: 150px;
			}
		}
	}
}
